import React, { useState, useEffect } from 'react';
import { styled } from 'styled-components';
import { TimerProps } from '../model/gameType';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../redux/store';
import { setUserRecord } from '../redux/propsSlice';

const Time = styled.div`
	line-height:2.5rem;
	font-size:2.5em;
	font-weight:600;
	color:orange;
`;

const Timer: React.FC<TimerProps> = () => {
	const dispatch = useDispatch();
	const run = useSelector((state: RootState) => state.props.run);
	const [seconds, setSeconds] = useState<number>(0);
  const [record, setRecord] = useState<string>("00:00");

	const formatTime = (time: number) => { //ÌÉÄÏù¥Î®∏ ÎëêÏûêÎ¶øÏàò(10Î≥¥Îã§ ÏûëÏúºÎ©¥ 0 Î∂ôÏó¨Ï£ºÍ∏∞)
		return time < 10 ? "0" + time : time.toString();
	}

	useEffect(() => { //Í≤åÏûÑ ÏãúÏûëÏãú ÌÉÄÏù¥Î®∏ ÏûëÎèô(run=trueÏù∏ Í≤ΩÏö∞)
		let timer: NodeJS.Timeout;
		if(run) { //ÌÉÄÏù¥Î®∏ ÏãúÏûë
			timer = setInterval(() => {
				setSeconds((prevSeconds) => prevSeconds + 1);
			}, 1000);

			const minutes = Math.floor(seconds / 60); //60Ï¥àÍ∞Ä ÎêòÎ©¥ 1Î∂ÑÏúºÎ°ú ÌëúÏãú
			const remainingSeconds = seconds % 60; //60Ï¥à Ïù¥ÌõÑ Îã§Ïãú 0Î∂ÄÌÑ∞ Ï¥àÎã®ÏúÑ ÏãúÏûë(seconds % 60 = ÎàÑÏ†ÅÏãúÍ∞ÑÏóêÏÑú 60ÏùÑ ÎÇòÎàÑÍ≥† ÎÇ®ÏùÄ Í∞í)
			const progress = formatTime(minutes) + ":" + formatTime(remainingSeconds);
			setRecord(progress);
			dispatch(setUserRecord(progress));
		} else if(!run) { //Ï¢ÖÎ£åÏãú Ï¥àÍ∏∞Ìôî
			setSeconds(0);
			setRecord("00:00");
		}

    return () => clearInterval(timer); // Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä Ïñ∏ÎßàÏö¥Ìä∏Îê† Îïå ÌÉÄÏù¥Î®∏ Ï†ïÎ¶¨
  }, [seconds, run, dispatch]);
	
	return (
		<Time>
			{record}
			{/* run(Í≤åÏûÑÏãúÏûë) Ïù¥ trueÏùºÎïå ÌÉÄÏù¥Î®∏ ÏûëÎèô */}
		</Time>
	)
}

export default Timer